<template>
    <article class="works will" id="works">

        <h2 class="prmryHeading">WORKS</h2>

        <div class="mt-10 md:flex justify-around flex-wrap">
            
            <article v-for="item in work_arr" :key="item.name"
                class="w-full px-4 py-6 mb-8 bg-gradient-to-br from-stone-50 to-stone-100 shadow-md md:respWork md:mr-auto"
            >
                <img class="w-[256px] h-[144px] rounder" :src="item.img" :alt="item.name">
                <!-- <div class="imageDummy bg-slate-300 w-[200px] h-[150px] mx-auto"></div> -->
                <h3 class="workTitle mt-8">{{ item.name }}</h3>
                <div class="flex mt-2">
                    <img v-show="item.avail.pc" src="../assets/pc.svg" class="w-5 h-5 mt-1">
                    <img v-show="item.avail.sm" src="../assets/sma_phone.svg" class="w-5 h-5 mt-1 ml-1">
                    <a :href="item.URL" class="block text-cyan-600 underline ml-2"> {{ item.URL }} </a>
                </div>

                <button @click="(ev) => handleInfoBtn(item, ev)" class="block mt-6 ml-auto mr-5 text-sm text-teal-700 underline">>詳細を開く</button>
            </article>

        </div>

        <article v-show="infoItem.name != ''" class="fixed w-screen md:w-[85vw] h-screen overflow-y-scroll overscroll-contain top-0 left-0 z-40">
            <div class="fixed -z-10 top-0 left-0 w-full h-full bg-slate-800 bg-opacity-90"></div>
            <section @click="(ev) => ev.stopPropagation()" class="w-4/5 md:px-40 h-200vh m-auto pt-40 bg-slate-600 bg-opacity-30 backdrop-blur-md">
                <img class="w-[256px] h-[144px] md:w-[512px] md:h-[288px] mx-auto rounded-sm border-2 border-slate-500" :src="infoItem.img" :alt="infoItem.name">
                <section class="text-slate-50 mt-6 md:mt-10 pb-20">

                    <h3 class="text-sm mb-1">作品名</h3>
                    <p class="scdryHeading ml-2"> {{ infoItem.name }} </p>

                    <section class="mt-6">
                        <h4 class="text-sm mb-1">開発言語、フレームワーク等</h4>
                        <ul class="list-none">
                            <li v-for="tool in infoItem.tool" :key="tool" class="font-semibold ml-2">{{ tool }}</li>
                        </ul>

                    </section>

                    <section class="mt-6">
                        <h4>対応機種 : 
                            {{ infoItem.avail.pc ? "PC " : "" }} {{ infoItem.avail.sm ? "スマートフォン" : "" }}
                        </h4>
                        <p class="mt-1">URL : <a :href="infoItem.URL" class="text-cyan-500 underline">{{ infoItem.URL }} </a></p>
                    </section>

                    <section class="mt-6">
                        <h4 class="text-sm mb-2">コメント</h4>
                        <p v-for="(text, idx) in infoItem.comment" :key="idx" class="mb-1 ml-1"> {{ text }} </p>
                    </section>
                    <button @click="(ev) => handleCloseBtn(ev)" class="block mt-12 ml-auto mr-2 text-sm text-slate-300 underline px-2 bg-slate-600 rounded">>閉じる</button>

                </section>
            </section>
        </article>

    </article>

</template>

<script setup>

    import { ref } from 'vue';

    const infoItem = ref(
        {
            name: "",
            img: "",
            avail: { pc: false, sm: false },
            URL: "",
            tool: [],
            comment: [""],
        },
    );

    // const date = new Date();
    // const dateString = date.getFullYear() + "_" + (date.getMonth() + 1) + "_" + date.getDate();

    const work_arr = [
        {
            name: "タイルパターン エディタ",
            img: require("../../public/tile_ss.webp"),
            avail: { pc: true, sm: false },
            URL: "https://sg1t.github.io/tileEditor/",
            tool: ["React", "Tailwind CSS", "Canvas API"],
            comment: [
                "タイルパターンの繰り返し元となる画像を制作するアプリです。",
                "ペンや消しゴム、直線/曲線ツールのほか、加筆部分を線対称に反映する機能も実装しました。これにより、タイルを並べた際にできる辺の齟齬や違和感を抑えることができます。",
                "手ブレ補正や直線ツールの角度固定など、使用感に関わる部分にも手を加えましたが、レイヤー機能は透明度の処理により動作が重くなるおそれにより実装を見送りました。"
            ],
        },
        {
            name: "メモアプリ",
            img: require("../../public/memo_ss.webp"),
            avail: { pc: true, sm: true },
            URL: "https://sg1t.github.io/memoApp/",
            tool: ["React", "React Router DOM", "Tailwind CSS"],
            comment: [
                "Reactへの理解を深めるために開発したメモアプリです。",
                "Vue.jsと異なりCSSを単一のファイルに含められないことにギャップを感じたため、Tailwind CSSの習得も並行して進めました。",
                "テキストエリアの自動調整は、内容が過不足なく収まる高さを二分探索で求めています。",
            ],
        },
        {
            name: "単語帳アプリ",
            img: require("../../public/fca_ss.webp"),
            avail: { pc: true, sm: true },
            URL: "https://sg1t.github.io/fca/",
            tool: ["Vue.js", "Vuex", "Firebase"],
            comment: [
                "職業訓練の実習にて制作した単語帳アプリです。ログイン機能により異なる端末からでも同じデータを利用できます。",
                "バックエンド関連の知識は皆無でしたが、公式のリファレンスやノウハウ共有サービスの投稿等を参考に開発を進め、ログイン情報をVuex(状態管理ライブラリ)のストアに保持する形で実装しました。",
                "新規登録にあたってメールアドレスの入力を求められますが、@以降のドメインが公式のものかつパスワードが6文字以上であれば「(今日の日付) + @gmail.com」のような架空のアドレスでも登録できます。",
            ],
        },
        {
            name: "英語彙テスト",
            img: require("../../public/vocab_ss.webp"),
            avail: { pc: true, sm: false },
            URL: "https://sg1t.github.io/vocabTester/",
            tool: ["Vue.js", "Fetch API"],
            comment: [
                "あるスマートフォン用の学習アプリに触発されて開発した、英単語の語彙力を測るWebアプリです。FetchAPIを用いた外部JSONデータ取得の復習も兼ねて開発しました。",
                "アイデア元となったアプリが四つの英単語からの択一式だったことに対し、正確なスペルをタイピングして回答する形式にしたかったことから、現状はPCのみに対応しています。",
            ],
        },
    ]
    function handleCloseBtn(ev) {
        ev.stopPropagation();
        infoItem.value = {
            name: "",
            img: "",
            avail: { pc: false, sm: false },
            URL: "",
            tool: [],
            comment: [""],
        }
    }

    function handleInfoBtn(obj, ev) {
        ev.stopPropagation();
        infoItem.value = obj;
    }


    window.addEventListener("click", handleCloseBtn);


</script>

